import {Component, NgZone} from '@angular/core';
import {ViewController, NavController} from 'ionic-angular';
declare let google;
import { ProfileProvider } from '../../providers/profile/profile';
import { SettingsProvider } from '../../providers/settings/settings';
import { IonicPage } from 'ionic-angular';
import { LanguageProvider } from '../../providers/language/language';
import { PopUpProvider } from '../../providers/pop-up/pop-up';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
@IonicPage()
@Component({
  templateUrl: 'autocomplete.html'
})

export class AutocompletePage {
  autocompleteItems;
  autocomplete;
  home: any;
  work: any;
  Lang: any;
  service = new google.maps.places.AutocompleteService();

  constructor (public viewCtrl: ViewController, public myGcode: GeocoderProvider, public pop: PopUpProvider, public lp: LanguageProvider, public settings: SettingsProvider,  private navCtrl: NavController, private ph: ProfileProvider, private zone: NgZone) {
    this.Lang = this.lp.translate();
    this.autocompleteItems = []
   
    this.autocomplete = {
      query: ''
    };
  }

Accept(){
  this.dismiss();
}
  

  dismiss() {
    this.viewCtrl.dismiss();
  }

  chooseItem(item: any) {
    this.viewCtrl.dismiss(item);
    this.ph.isHome = true
  }
  
  updateSearch() {
    this.ph.isHome = false
    if (this.autocomplete.query == '') {
      this.autocompleteItems = [];
      return;
    }
    let me = this;
    this.service.getPlacePredictions({ input: this.autocomplete.query, componentRestrictions: {country: [this.settings.appCountryCode]} }, (predictions, status) => {
      me.autocompleteItems = []; 
      me.zone.run( () => {
        predictions.forEach( (prediction) => {
          if (prediction != null)
          me.autocompleteItems.push(prediction.description);
        });
      });
    });
  }
}


